import { Injectable, EventEmitter } from '@angular/core';
import {Recipe} from './recipe';
import {Ingrediant} from '../ingrediant';
import {Http, Response} from "@angular/http";
import 'rxjs/Rx';


@Injectable()
export class RecipeService {
 private recipe:Recipe[] =[new Recipe('Schnitzel', 'Very tasty', 'http://www.kiddle.co/kids-search-robot-10.png',
  [new Ingrediant('French Fries',1),new Ingrediant('Pork Meat',2)]),
    new Recipe('Summer Salad', 'Okayish', 'http://ohmyveggies.com/wp-content/uploads/2013/06/the_perfect_summer_salad.jpg', [])
  ];
  recipesChanged = new EventEmitter<Recipe[]>(); 

  getData() {
    return this.recipe;
  }
  getRecipe(id:number) {
    return this.recipe[id];

  }
  deleteRecipe(recipe:Recipe) {
    this.recipe.splice(this.recipe.indexOf(recipe),1)
  }
  addRecipe(res:Recipe) {
    this.recipe.push(res);
  }
  editRecipe(oldRecipe:Recipe, newRecipe:Recipe) {
     this.recipe[this.recipe.indexOf(oldRecipe)]=newRecipe;
  }
  
  storeRecipe() {
    const body =JSON.stringify(this.recipe);
       return this.http.put('https://recipebook-2c4d1.firebaseio.com/Recipe.json',body);

  } 

  retriveRecipe() {
     return this.http.get('https://recipebook-2c4d1.firebaseio.com/Recipe.json').map(
       (response:Response) => response.json()
     ).subscribe(
       (data:Recipe[]) => {this.recipe=data;
      this.recipesChanged.emit(this.recipe) }
     );
      
  }

  constructor(private http:Http) { }

}



