import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from "@angular/router";
import { RecipeService } from "../recipe.service";
import { Recipe } from "../recipe";
import { FormArray, FormGroup, FormControl, Validators } from "@angular/forms";


@Component({
  selector: 'rb-recipe-edit',
  templateUrl: './recipe-edit.component.html',
  styles: []
})
export class RecipeEditComponent implements OnInit {
  recipeIndex:number;
  isNew:boolean =true;
  private recipe:Recipe;
   recipeForm:FormGroup;

  constructor(private actR:ActivatedRoute, private RecSer:RecipeService,private router:Router ) { }

  ngOnInit() {
    this.actR.params.subscribe(
      (params:any) => {
        if(params.hasOwnProperty('id')) {
          this.recipeIndex=+params['id'];
          this.isNew=false;
          this.recipe=  this.RecSer.getRecipe(this.recipeIndex);
        }
        else{
          this.isNew=true;
        }
        this.initForm();
       
      }
    );
  }

  navigateBack() {
    this.router.navigate(['../']);
  }
  addIngrediant(name:string, price:string) {
      (<FormArray>this.recipeForm.controls['ingrediants']).push(
        new FormGroup({
         'name': new FormControl(name,Validators.required),
            'price': new FormControl(price,
                     [Validators.required,Validators.pattern("\\d+")]) 
        })
      );
  }

  removeIngrediant (index:number) {
     (<FormArray>this.recipeForm.controls['ingrediants']).removeAt(index);
  }
  
  onSubmit() {
    const newRecipe = this.recipeForm.value;
    if(this.isNew) {
      this.RecSer.addRecipe(newRecipe);
    }
    else {
      
      this.RecSer.editRecipe(this.recipe,newRecipe);
    }
    this.navigateBack();
  }

  private initForm() {
   let recipeName ='';
   let recipeImageUrl ='';
   let recipeContent ='';
   let recipeIngrediants:FormArray =new FormArray([]);

    if(!this.isNew) {
      if(this.recipe.hasOwnProperty('ingrediants')) {
      for(let i=0; i<this.recipe.ingrediants.length; i++) {
        recipeIngrediants.push(
          new FormGroup({
            'name': new FormControl(this.recipe.ingrediants[i].name,Validators.required),
            'price': new FormControl(this.recipe.ingrediants[i].price,
                     [Validators.required,Validators.pattern("\\d+")])
          })
        );  }

      }
      recipeName =this.recipe.name;
      recipeImageUrl=this.recipe.imagePath;
      recipeContent=this.recipe.describtion;

    }
     
     this.recipeForm =new FormGroup({
       'name': new FormControl(recipeName, Validators.required),
       'imagePath': new FormControl(recipeImageUrl, Validators.required),
       'describtion': new FormControl(recipeContent , Validators.required),
       'ingrediants': recipeIngrediants
     });
    

  }

}



