import { RecipeComponent } from "app/recipe/recipe.component";
import { RouterModule} from "@angular/router";
import { ShoppingListComponent } from "app/shopping-list/shopping-list.component";
import { RECIPE_ROUTES } from "app/recipe/recipe.routes";

const APP_ROUTS = [
    {path:'',redirectTo:'/recipes', pathMatch:'full'},
    {path:'recipes',component:RecipeComponent, children: RECIPE_ROUTES},
    {path:'shopping-list', component:ShoppingListComponent},
    {path:'**', redirectTo:'/recipes', pathMatch:'full'}
];
export const routing = RouterModule.forRoot(APP_ROUTS);


