import { Component, OnInit,Input,OnChanges,EventEmitter,Output } from '@angular/core';
import { NgForm } from "@angular/forms";
import { Ingrediant } from "../ingrediant";
import { ShoplistService} from './shoplist.service'

@Component({
  selector: 'rb-shopping-list-add',
  templateUrl: './shopping-list-add.component.html',
  styleUrls: ['./shopping-list-add.component.css']
})
export class ShoppingListAddComponent implements OnInit, OnChanges {
  ngForm:NgForm;
  isAdd =true;
  
  @Input() item:Ingrediant;
  @Output() cleared =new EventEmitter();
    

  constructor(private sls:ShoplistService) { }

  ngOnInit() {
  
  }
  ngOnChanges(changes) {
       
      if(changes.item.currentValue===null) {
        this.item={name:null, price:null}
        this.isAdd=true;
      }
      else {
        this.isAdd=false;
      }
  }
  onSubmit(ingrediant:Ingrediant) {
    const newIngrediant = new Ingrediant(ingrediant.name,ingrediant.price)
     if(!this.isAdd){
       this.sls.editItems(this.item,ingrediant);
       this.clearEdit();
     }
     else {
       this.item = ingrediant ;
        this.sls.addItems(this.item);
     }
    
  }
  onDeleteItem(item:Ingrediant) {
    this.sls.deleteItems(item);
    this.clearEdit();
  }
  clearEdit() {
    this.isAdd=true;
    this.cleared.emit(null);
  
  }

}



