import { Component, OnInit } from '@angular/core';
import {Recipe } from '../recipe';
import {ShoplistService} from '../../shopping-list/shoplist.service';
import {RecipeService} from '../recipe.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'rb-recipe-details',
  templateUrl: './recipe-details.component.html',
  styles: []
})
export class RecipeDetailsComponent implements OnInit {
  constructor(public sls : ShoplistService, 
              private router:Router, 
              private actR:ActivatedRoute,
              private recipeSer:RecipeService ) { 
    //router.navigate(['/recipes',])
  }
 selectedRecipe: Recipe;
 private recipeIndex:number;

 onEdit() {
 this.router.navigate(['/recipes',this.recipeIndex , 'edit']);
}
onDelete() {
  this.recipeSer.deleteRecipe(this.selectedRecipe);
  this.router.navigate(['/recipes']);
}
 addToShopList() {
    //alert('hai');
    this.sls.addItem(this.selectedRecipe.ingrediants);
    //alert(this.selectedRecipe.ingrediants);
  }
  

  ngOnInit() {
    this.actR.params.subscribe(
      (param)=>{
        this.recipeIndex=param['id'];
        this.selectedRecipe=this.recipeSer.getRecipe(this.recipeIndex);
      }
    );
    
    
  }
 

}


