import { Injectable } from '@angular/core';
import {Ingrediant} from '../ingrediant';

@Injectable()
export class ShoplistService {
  public items:Ingrediant[] =[];

   getItem () {
     return this.items;
   }
   addItem (item:Ingrediant[]) {
      Array.prototype.push.apply(this.items,item);
   }
   addItems (item:Ingrediant) {
      this.items.push(item);
   }
   editItems(oldItem:Ingrediant, newItem:Ingrediant) {
        this.items[this.items.indexOf(oldItem)] =newItem;
   }
   deleteItems(item:Ingrediant) {
     this.items.splice(this.items.indexOf(item));
   }

  constructor() { }

}



